import React from 'react'
import {
  Card,
  CardBlock,
  CardHeader,
  Form,
  FormGroup,
  Col,
  FormControlLabel,
  Input,
  Select,
  SelectOption,
  Checkbox,
  InlineSelect,
  InlineSelectOption,
  ButtonGroup,
  Button,
  Upload,
  Icon,
} from 'react-impression'
import styles from './CreateOrder.module.scss'
import Counter from '../components/Counter'
import classNames from 'classnames'

const address = ['1', '2', '3']
const insuranceType = ['1', '2', '3']

export default class CreateOrder extends React.Component {
  state = {
    deliveryTimeType: 1,
    insurance: 1,
    previewUrl: '',
  }

  handleDeliveryTimeType = val => {
    this.setState({
      deliveryTimeType: val,
    })
  }

  handleInsurance = val => {
    this.setState({
      insurance: val,
    })
  }

  handlePreviewChange = event => {
    const file = event.target.files && event.target.files[0]

    if (file) {
      console.log('', event.currentTarget.value)
      const reader = new window.FileReader()
      reader.onload = e => {
        this.setState({
          previewUrl: e.currentTarget.result,
        })
      }
      reader.readAsDataURL(file)
    }
  }

  render() {
    const { deliveryTimeType, insurance } = this.state

    return (
      <Card>
        <CardHeader className='text-left'></CardHeader>
        <CardBlock>
          <Form type='horizontal'>
            <FormGroup>
              <Col col='2' className='text-right'>
                <FormControlLabel>:</FormControlLabel>
              </Col>
              <Col col='10'>
                <Select
                  className={styles['address-select']}
                  searchable
                  placeholder=''
                >
                  {address.map((each, index) => (
                    <SelectOption value={each} key={index}>
                      {each}
                    </SelectOption>
                  ))}
                </Select>
              </Col>
            </FormGroup>
            <FormGroup>
              <Col col='2' className='text-right'>
                <FormControlLabel>:</FormControlLabel>
              </Col>
              <Col col='10'>
                <Input type='text' style={{ width: 500 }} />
              </Col>
            </FormGroup>
            <FormGroup>
              <Col col='2' className='text-right'>
                <FormControlLabel>:</FormControlLabel>
              </Col>
              <Col col='10'>
                <Input type='text' style={{ width: 200 }} />
              </Col>
            </FormGroup>
            <FormGroup>
              <Col col='2' className='text-right'>
                <FormControlLabel>:</FormControlLabel>
              </Col>
              <Col col='10'>
                <Input type='text' style={{ width: 200 }} />
              </Col>
            </FormGroup>
            <FormGroup>
              <Col col='10' offset='2'>
                <Checkbox></Checkbox>
              </Col>
            </FormGroup>
            <FormGroup>
              <Col
                col='2'
                className={classNames('text-right', styles['inline-label'])}
              >
                <strong>:</strong>
              </Col>
              <Col col='5' className={styles['inline-content']}>
                <InlineSelect
                  defaultValue={'1'}
                  onChange={this.selectZoneHandle}
                >
                  <InlineSelectOption value='1'></InlineSelectOption>
                  <InlineSelectOption value='2'></InlineSelectOption>
                  <InlineSelectOption value='3'></InlineSelectOption>
                  <InlineSelectOption value='4'></InlineSelectOption>
                  <InlineSelectOption value='5'></InlineSelectOption>
                  <InlineSelectOption value='6'></InlineSelectOption>
                  <InlineSelectOption value='7'></InlineSelectOption>
                  <InlineSelectOption value='8'></InlineSelectOption>
                  <InlineSelectOption value='9'></InlineSelectOption>
                </InlineSelect>
              </Col>
            </FormGroup>
            <FormGroup>
              <Col col='2' className='text-right'>
                <FormControlLabel>:</FormControlLabel>
              </Col>
              <Col col='4'>
                <Counter />
              </Col>
            </FormGroup>
            <FormGroup>
              <Col col='2' className='text-right'>
                <FormControlLabel>:</FormControlLabel>
              </Col>
              <Col col='10'>
                <Input type='textarea' style={{ height: 90, width: 500 }} />
              </Col>
            </FormGroup>
            <FormGroup>
              <Col col='2' className='text-right'>
                <FormControlLabel>:</FormControlLabel>
              </Col>
              <Col col='3'>
                <ButtonGroup
                  theme='default'
                  activeKey={deliveryTimeType}
                  onSelect={this.handleDeliveryTimeType}
                >
                  <Button eventKey={0}></Button>
                  <Button eventKey={1}></Button>
                </ButtonGroup>
              </Col>
              {deliveryTimeType === 1 && (
                <span>
                  <Input
                    type='date'
                    defaultValue='2016-05-29'
                    style={{ width: 224 }}
                  />
                </span>
              )}
            </FormGroup>
            <FormGroup>
              <Col col='2' className='text-right'>
                <FormControlLabel>:</FormControlLabel>
              </Col>
              <Col col='3'>
                <ButtonGroup
                  theme='default'
                  activeKey={insurance}
                  onSelect={this.handleInsurance}
                >
                  <Button eventKey={0}></Button>
                  <Button eventKey={1}></Button>
                </ButtonGroup>
              </Col>
              {insurance === 1 && (
                <Select placeholder=''>
                  {insuranceType.map((each, index) => (
                    <SelectOption value={each} key={index}>
                      {each}
                    </SelectOption>
                  ))}
                </Select>
              )}
            </FormGroup>
            <FormGroup>
              <Col col='2' className='text-right'>
                <FormControlLabel>:</FormControlLabel>
              </Col>
              <Col col='10'>
                <Upload
                  src={this.state.previewUrl}
                  preview
                  message=''
                  onChange={this.handlePreviewChange}
                >
                  <Icon type='plus' />
                </Upload>
              </Col>
            </FormGroup>
            <FormGroup style={{ justifyContent: 'center' }}>
              <Button theme='default' className={styles['cancel-button']}>
                
              </Button>
              <Button theme='primary'></Button>
            </FormGroup>
          </Form>
        </CardBlock>
      </Card>
    )
  }
}
