import React from 'react'
import {
  Card,
  CardBlock,
  CardHeader,
  Form,
  FormGroup,
  Col,
  FormControlLabel,
  Input,
  Button,
  Message,
} from 'react-impression'

export default class ChangePassWord extends React.Component {
  state = {
    password: '',
    confirmPassword: '',
  }

  checkPasswd = () => {
    const values = this.state.password

    if (!values) {
      Message.error('')
      return
    } else if (values.length < 8) {
      Message.error('8')
      return
    } else if (values.length > 16) {
      Message.error('16')
      return
    }

    return true
  }

  checkPasswd2 = () => {
    const { confirmPassword: values, password } = this.state

    if (!values) {
      Message.error('')
      return
    } else if (values !== password) {
      Message.error('')
      return
    }

    return true
  }

  validateAllFormField = () => {
    if (!this.checkPasswd() || !this.checkPasswd2()) return
    Message.success('')
  }

  render() {
    const { password, confirmPassword } = this.state

    return (
      <Card>
        <CardHeader className='text-left'></CardHeader>
        <CardBlock>
          <Form type='horizontal'>
            <FormGroup>
              <Col col='2' className='text-right'>
                <FormControlLabel>:</FormControlLabel>
              </Col>
              <Col col='8'>
                <Input
                  type='text'
                  placeholder=''
                  value={password}
                  onChange={val => this.setState({ password: val })}
                />
              </Col>
            </FormGroup>
            <FormGroup>
              <Col col='2' className='text-right'>
                <FormControlLabel>:</FormControlLabel>
              </Col>
              <Col col='8'>
                <Input
                  type='text'
                  placeholder=''
                  value={confirmPassword}
                  onChange={val => this.setState({ confirmPassword: val })}
                />
              </Col>
            </FormGroup>
            <FormGroup style={{ justifyContent: 'center' }}>
              <Button theme='primary' onClick={this.validateAllFormField}>
                
              </Button>
            </FormGroup>
          </Form>
        </CardBlock>
      </Card>
    )
  }
}
